.schedule {
  width: 100%;       /* fills available space */
  max-width: 100%;   /* prevent desktop scaling on mobile */
  margin: 0 auto;
  padding: 1rem;
}

.day h2 {
  background: #111;
  color: #b8893d;
  padding: 1rem;          
  margin: 0.75rem 0;
  cursor: pointer;
  border-radius: 6px;
  font-size: 1.25rem;
}

.day ul {
  list-style: none;
  padding: 0.75rem 1rem;
  margin: 0;
  background: #222;
  border-radius: 6px;
}

.day li {
  padding: 0.75rem 0;
  border-bottom: 1px solid #444;
}

.day li:last-child {
  border-bottom: none;
}

.time {
  font-weight: bold;
  color: #fff;
}

.class-name {
  color: #b8893d;
}

.day.open h2 {
  background-color: #b8893d;
  color: #000;
}

/* Mobile adjustments */
@media (max-width: 768px) {
  .schedule {
    width: 100%;
    max-width: 100%;
    padding: 1rem;
    margin: 0 auto;
  }

  .day h2 {
    font-size: 1.1rem;
    padding: 0.9rem;
  }

  .day li {
    padding: 0.6rem 0;
  }
}

/* Optional: desktop-only max-width */
@media (min-width: 768px) {
  .schedule {
    max-width: 600px; /* only applies on wider screens */
  }
}
